import { h, app } from "hyperapp";
import "./style.css";

const MEDIA_CONST = {
  mandatory: { OfferToReceiveAudio: true, OfferToReceiveVideo: true },
};

const ICE_SERVER = { url: "stun:stun.l.google.com:19302" };

const state = {
  count: 0,
  selfStream: null,
  peer: null,
  selfSDP: null,
  partnerSDP: null,
  partnerStream: null,
  selfICEs: [],
  partnerICEs: [],
};

const actions = {
  down: () => (state) => ({ count: state.count - 1 }),
  up: () => (state) => ({ count: state.count + 1 }),

  startVideo: () => async (state, actions) => {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
    actions.setSelfStream(stream);
  },

  setSelfStream: (stream) => {
    document.querySelector("#self-video").srcObject = stream;
    return {
      selfStream: stream,
    };
  },

  connect: () => (state, actions) => {
    const peer = (() => {
      // peer connection
      const p = new RTCPeerConnection({
        iceServers: [ICE_SERVER],
      });
      p.onicecandidate = (e) => {
        console.log("on candidate", e.candidate);
        if (e.candidate) {
          actions.setSelfICE(e.candidate);
        }
      };
      p.addStream(state.selfStream);
      p.onaddstream = (e) => {
        actions.setPartnerStream(e.stream);
      };
      return p;
    })();
    // offer
    peer.createOffer(MEDIA_CONST).then((sd) => {
      actions.setSelfSDP(sd);
    });
    return { peer };
  },

  setSelfSDP: (selfSDP) => (state) => {
    state.peer.setLocalDescription(selfSDP);
    return { selfSDP };
  },

  setPartnerStream: (stream) => {
    document.querySelector("#partner-video").srcObject = stream;
    return {
      partnerStream: stream,
    };
  },

  onReceivePartnerSDP: (e) => (state, actions) => {
    const partnerSDP = JSON.parse(e.target.value);
    return { partnerSDP };
  },

  setPartnerSDP: () => (state, actions) => {
    const { partnerSDP } = state;
    let { peer } = state;
    if (peer) {
      // answer
      peer.setRemoteDescription(new RTCSessionDescription(partnerSDP));
    } else {
      // offeranswer
      peer = (() => {
        // create peer connection
        const p = new RTCPeerConnection({ iceServers: [ICE_SERVER] });
        p.onicecandidate = (e) => {
          if (e.candidate) {
            actions.setSelfICE(e.candidate);
          }
        };
        p.addStream(state.selfStream);
        p.onaddstream = (e) => {
          actions.setPartnerStream(e.stream);
        };
        return p;
      })();

      peer.setRemoteDescription(new RTCSessionDescription(partnerSDP));
      peer.createAnswer(MEDIA_CONST).then((selfSDP) => {
        actions.setSelfSDP(selfSDP);
      });
    }
    return { partnerSDP, peer };
  },

  setSelfICE: (selfICE) => (state) => {
    state.selfICEs.push(selfICE);
    return { selfICEs: state.selfICEs };
  },

  onReceivePartnerICE: (e) => (state, actions) => {
    const partnerICEs = JSON.parse(e.target.value);
    return { partnerICEs };
  },

  setPartnerICE: () => (state, actions) => {
    const { partnerICEs, peer } = state;
    partnerICEs.forEach((ice) => {
      peer.addIceCandidate(ice);
    });
    return {};
  },
};

const view = (state, actions) => {
  console.log("update", state);
  return (
    <main>
      <button type="button" onclick={actions.startVideo}>
        
      </button>
      <button type="button" onclick={actions.connect}>
        
      </button>
      <div>
        <video id="self-video" autoplay controls></video>
        <video id="partner-video" autoplay controls></video>
      </div>
      <div>
        <p>SDP()</p>
        <textarea rows="5" cols="100" disabled>
          {JSON.stringify(state.selfSDP)}
        </textarea>
      </div>
      <div>
        <p>SDP()</p>
        <textarea
          rows="5"
          cols="100"
          onchange={actions.onReceivePartnerSDP}
        ></textarea>
        <button type="button" onclick={actions.setPartnerSDP}>
          
        </button>
      </div>
      <div>
        <p>ICE Candidate()</p>
        <textarea rows="5" cols="100" disabled>
          {JSON.stringify(state.selfICEs)}
        </textarea>
      </div>
      <div>
        <p>ICE Candidate()</p>
        <textarea
          rows="5"
          cols="100"
          onchange={actions.onReceivePartnerICE}
        ></textarea>
        <button type="button" onclick={actions.setPartnerICE}>
          
        </button>
      </div>
    </main>
  );
};

export const main = app(state, actions, view, document.body);
